@font-face {
  font-family: Avenir;
  src: url(fonts/Avenir-Book.ttf);
}

* {
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 16px;
  font-family: Avenir, sans-serif;
}

.search-form {
  display: flex;
  justify-content: center;
  margin: 20px auto 0;
}

.search-form_big {
  width: 100%;
  height: 70px;
}

.search-form__label {
  position: relative;
  display: block;
  width: 100%;
}

.search-form__label::after {
  position: absolute;
  content: "";
  display: block;
}

.search-form__label_big::after {
  background-image: url(images/Search.svg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.search-form__input {
  width: 100%;
  height: 100%;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  font-weight: 300;
  font-family: inherit;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  outline: none;
}

.search-form__input:hover {
  border-radius: 4px;
  border: 1px solid #E1E7ED;
  background: #FFF;
  box-shadow: 0 3px 8px 0 rgba(61, 78, 97, 0.20);
}

.search-form__input:focus {
  border-radius: 3px;
  border: 1px solid #E1E7ED;
  background: linear-gradient(180deg, #FFF 0%, #F6F6F7 100%);
}

.search-form__input_big {
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
}

.search-form_small {
  width: 100%;
  height: 42px;
}

.search-form__label_small::after {
  background-image: url(images/Search.svg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.search-form__input_small {
  padding-left: 33px;
  font-size: 14px;
  line-height: 19px;
}
